import { useState, useEffect } from "react";
import ReactMarkdown from "react-markdown";
import { app_background } from "./data/markdown";

import { useAuthState } from "react-firebase-hooks/auth";

import Button from "react-bootstrap/Button";

import Note from "./Note";

const fake_notes = [
  { id: 0, note: "fake note #1", date: new Date() },
  { id: 1, note: "fake note #2", date: new Date() },
  {
    id: 2,
    note:
      "i'm getting tired of my classes but i have to continue at this point",
    date: new Date(),
  },
];

const App = () => {
  const [edit, setEdit] = useState(false);
  const [notes, setNotes] = useState(fake_notes);
  const [currNote, setCurrNote] = useState(null);
  // const [user, loading, error] = useAuthState(auth);
  const [user, setUser] = useState(null);

  const changeEditStatus = (id, note) => {
    setCurrNote(id);
    if (id !== currNote) {
      setEdit(!edit);
    }
  };

  const saveNote = (id) => {
    setEdit(!edit);
    setCurrNote(null);
    // pass notes to firebase
  };

  const addNote = () => {
    setNotes([...notes, { id: notes.length, note: "", date: new Date() }]);
  };

  useEffect(() => {
    if (!notes) {
      // grab notes from firebase
    }
  }, [notes]);

  return (
    <div className="home">
      {user ? (
        <div id="content">
          <ReactMarkdown className="background" source={app_background} />
          <div className="container">
            <div className="row justify-content-md-center">
              {notes.map(({ id, note, date }) => (
                <Note
                  key={id}
                  id={id}
                  note={note}
                  currNote={currNote}
                  date={date}
                  changeEditStatus={changeEditStatus}
                  edit={edit}
                  saveNote={saveNote}
                />
              ))}
            </div>
            <Button onClick={addNote}>Add note!</Button>
          </div>
        </div>
      ) : (
        <div>Login!</div>
      )}
    </div>
  );
};

export default App;
