import Button from "react-bootstrap/Button";
import InputGroup from "react-bootstrap/InputGroup";
import FormControl from "react-bootstrap/FormControl";
import Form from "react-bootstrap/Form";

const Note = ({
  id,
  note,
  date,
  changeEditStatus,
  edit,
  currNote,
  saveNote,
}) => {
  const editNote = (e) => {
    changeEditStatus(id, e.target.value);
  };

  const isCurrNote = currNote === id;

  return (
    <div className="col-3 note" key={id}>
      <Form>
        <Form.Group controlId="form-input">
          <FormControl
            defaultValue={note}
            onChange={editNote}
            as="textarea"
            rows={5}
          />
          <InputGroup.Append>
            {edit && isCurrNote && (
              <Button className="save-btn" onClick={saveNote}>
                Save
              </Button>
            )}
          </InputGroup.Append>
        </Form.Group>
      </Form>
      <div className="date">{date.toLocaleString()}</div>
    </div>
  );
};

export default Note;
